
.text_slider{
  width: 90%;
  margin: auto;
  padding-top: 15px;
  color: white;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  font-size: small;
}
.upercase {
  text-transform: uppercase;
}
.span_title {
  width: 90%;
  margin: auto;
  color: white;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  font-size: xx-small;
  padding-top: 5px;
  border-bottom: 1px solid #edf0a39f;
}

.index {
  z-index: -1;
}


/* content */
.image{
  height:80vh;
  width:100%;
  background-attachment:fixed;
  background-size:cover;
}
#spritzer{
  background: transparent;
  background-attachment:fixed;
  background-size:cover;
}
#uberuns{
  position: absolute;
  top:70%;
  height:100%;
  width:100%;
    background:rgba(0, 0, 0, 0.486);
        position:relative;
}

#uberuns:after {
  height:100%;
  width:100%;
  position:absolute;    
  left:0;
  top:0;
  z-index:-1;
}

.tag_slider{
  color: #dfdd60;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  font-size: xx-small;
  margin-left: 10px;
}

.carousel-inner .item { transition: all 0.5s ease !important; }